import React, { useMemo } from "react";
import { TYPE_NETWORK } from "../../../../../constants/enum";
import { ImgWifiNone } from "../../../../../assets/image";
import { ReactComponent as ImgWifi } from "../../../../../assets/image/img-wifi.svg";

import classNames from "classnames";
import { MAP_NETWORD_DESCRIPTION } from "../../../../../constants";

interface INetworkInfo {
  score: number;
  isConnected: boolean;
}
const NetworkInfo = ({ score, isConnected }: INetworkInfo) => {
  const typeNetwork = useMemo(() => {
    return isConnected
      ? score >= 75
        ? TYPE_NETWORK.HIGH
        : score > 25
        ? TYPE_NETWORK.MEDIUM
        : TYPE_NETWORK.LOW
      : TYPE_NETWORK.NONE;
  }, [isConnected, score]);

  if (!typeNetwork) return <></>;
  return (
    <div className="flex h-[183px] flex-col items-center x-3 justify-center">
      {typeNetwork !== TYPE_NETWORK.NONE ? (
        <ImgWifi className={`w-[71px] h-[63px] img-wifi-${typeNetwork}`} />
      ) : (
        <img width={71} height={63} alt="img-wifi-none" src={ImgWifiNone} />
      )}

      <div
        className={classNames({
          "mb-3 mt-4": typeNetwork !== TYPE_NETWORK.NONE,
        })}
        hidden={typeNetwork === TYPE_NETWORK.NONE}
      >
        <span
          className={classNames("text-14px font-bold", {
            "text-green": typeNetwork === TYPE_NETWORK.HIGH,
            "text-yellow": typeNetwork === TYPE_NETWORK.MEDIUM,
            "text-red": typeNetwork === TYPE_NETWORK.LOW,
          })}
        >
          Network Quality: {score?.toFixed()}%
        </span>
      </div>
      <div
        className={classNames("span text-neutral-110 text-14px text-center", {
          "mt-4": typeNetwork === TYPE_NETWORK.NONE,
        })}
      >
        {MAP_NETWORD_DESCRIPTION[typeNetwork]}
      </div>
    </div>
  );
};

export default NetworkInfo;
