/*global chrome*/
import React, { Suspense, useEffect } from "react";
import "./App.css";
import LoginView from "./modules/Login/views";
import { Route, Routes, useNavigate } from "react-router-dom";
import { ROUTER_EXTENSION } from "./constants/router";
import NetworkConnectionView from "./modules/ActiveScreen/views";
import { useSetRecoilState } from "recoil";
import { tokenState } from "./adapter/recoil/common/state";
import browser from "webextension-polyfill";
import Loading from "./shared/components/loading";

function App() {
  const navigate = useNavigate();
  const setToken = useSetRecoilState(tokenState);

  const navigateToLogin = () => {
    navigate(ROUTER_EXTENSION.LOGIN);
  };

  const init = () => {
    browser.storage.local.get(["np_token"]).then((data) => {
      let token = data?.["np_token"];
      if (token) {
        browser.runtime.sendMessage({
          np_token: token,
        });
        setToken(token);
        navigate(ROUTER_EXTENSION.DASHBOARD);
      } else {
        navigateToLogin();
      }
    });
  };
  useEffect(() => {
    init();
  }, []);

  return (
    <Suspense fallback={<Loading />}>
      <Routes>
        <Route path={ROUTER_EXTENSION.LOGIN} element={<LoginView />} />
        <Route
          path={ROUTER_EXTENSION.DASHBOARD}
          element={<NetworkConnectionView />}
        />
      </Routes>
    </Suspense>
  );
}

export default App;
