import classNames from "classnames";
import React from "react";

export const StatusConnection = ({ isConnected }) => {
  return (
    <div className="rounded-[40px] bg-[#F7F7F7] px-4 py-[6px] flex items-center w-fit">
      <div
        className={classNames("w-[10px] h-[10px] rounded-full mr-1", {
          "bg-green": isConnected,
          "bg-red": !isConnected,
        })}
      ></div>
      <span
        className={classNames("font-bold", {
          "text-green": isConnected,
          "text-red": !isConnected,
        })}
      >
        {isConnected ? "Connected" : "Disconnected"}
      </span>
    </div>
  );
};
